nav {
  background-color: #F8F8F8;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1000; }
  nav ul {
    height: 70px;
    margin: 0;
    padding: 0; }
    nav ul li {
      list-style: none;
      cursor: pointer;
      color: #686868; }
  nav .movil {
    height: 70px; }
    nav .movil .icono-menu {
      cursor: pointer;
      padding: .8rem .5rem; }
      nav .movil .icono-menu:focus {
        outline: none; }
      nav .movil .icono-menu:hover span:nth-child(2) {
        width: 30px; }
      nav .movil .icono-menu.active span {
        width: 25px;
        -webkit-transform: translateY(3px) rotate(45deg);
                transform: translateY(3px) rotate(45deg); }
        nav .movil .icono-menu.active span:nth-child(2) {
          -webkit-transform: translateY(-6px) rotate(-45deg);
                  transform: translateY(-6px) rotate(-45deg);
          width: 25px; }
      nav .movil .icono-menu span {
        height: 3px;
        width: 30px;
        background-color: #040082;
        -webkit-transition: all .15s ease-in-out;
        transition: all .15s ease-in-out;
        margin-bottom: .4rem; }
        nav .movil .icono-menu span:nth-child(2) {
          width: 20px;
          margin: 0; }
  nav .slide {
    position: absolute;
    top: 70px;
    right: 0;
    width: 50%;
    height: calc(100vh - 70px);
    background-color: #F8F8F8; }
    nav .slide ul {
      -webkit-transform: translateY(-70px);
              transform: translateY(-70px); }
      nav .slide ul li:not(:last-child) {
        margin-bottom: 1.5rem; }

